.task-board span.task-icon-age-total,
.task-board span.task-icon-age-column {
  border-color: $primary-color;
}

span {
  &.task-icon-age-total,
  &.task-icon-age-column {
    opacity: 1;
    padding: 2px 5px;
    border-color: $primary-color;
  }

  &.task-icon-age-column {
    margin-left: -3px;
  }
}
